import React, { Component } from "react"
import { TextareaField } from "@components/shared/Fields/TextareaField"
import { TextField } from "@components/shared/Fields/TextField"
import { Col, Row, Button, Form, FormGroup, FormLabel, FormControl } from "react-bootstrap"
import { Breadcrumb } from "@components/shared/Breadcrumb"
import { getProjects, updateProject } from "@actions/project"
import { getConsultants } from "@actions/consultant"
import { selectConsultants } from "@selectors/consultant"
import { selectProject, loadingProject, projectErrors } from "@selectors/project"
import { connect } from "react-redux"
import { withRouter } from "react-router-dom"
import Sidebar from "../../Sidebar"
import { Loading } from "@components/shared/Loading"
import styles from "./Projects.module.css"

class EditProject extends Component {
  state = {
    loading: true,
    siteAddressName: "",
    sitePhoneNumber: "",
    description: "",
    selectedConsultants: [],
  }

  componentDidMount() {
    this.props.getProjects()
    this.props.getConsultants()
    const {
      project: { description, site, consultants },
    } = this.props

    this.setState({
      siteAddressName: site.addressName,
      sitePhoneNumber: site.phoneNumber,
      description,
      selectedConsultants: consultants.map(item => item.id),
    })
  }

  onChange = e => {
    this.setState({ [e.target.name]: e.target.value })
    const { selectedConsultants } = this.state
    if (e.target.type === "checkbox") {
      if (e.target.checked) {
        this.setState({ selectedConsultants: [...selectedConsultants, parseInt(e.target.value)] })
      } else {
        this.setState({
          selectedConsultants: selectedConsultants.filter(item => item !== parseInt(e.target.value)),
        })
      }
    }
  }

  onSubmit = async e => {
    e.preventDefault()
    const { siteAddressName, sitePhoneNumber, description, selectedConsultants } = this.state
    const {
      updateProject,
      project: { customId, id },
      history,
    } = this.props

    await updateProject(customId, {
      consultants: selectedConsultants,
      description,
      sitePhoneNumber,
      siteAddressName,
    })
    history.push(`/dashboard/project/${id}`)
  }

  render() {
    const {
      loading,
      project: {
        id,
        consultant: { firstName, lastName },
        addressBook: { addressName, companyName },
      },
      project,
      consultants,
    } = this.props
    const { siteAddressName, sitePhoneNumber, description, selectedConsultants } = this.state

    if (loading) return <Loading />
    if (!project) return null
    return (
      <section className="main">
        <Row>
          <Col>
            <Sidebar />
          </Col>
          <Col lg="11" className="container-left">
            <Breadcrumb />
            <div className="ms-panel">
              <div className="ms-panel-header">
                <h6>
                  Project {id}
                  <a className={styles.href}>
                    &nbsp; by {firstName} {lastName}
                  </a>
                </h6>
                <Form className={styles.form} noValidate onSubmit={this.onSubmit}>
                  <TextField
                    name="companyName"
                    label="Company Name"
                    value={companyName}
                    placeholder="Ex: Intel"
                    onChange={this.onChange}
                    disabled
                  />
                  <TextField
                    name="addressName"
                    label="Address Name"
                    value={addressName}
                    placeholder="Ex: 33 Holborn, London, UK"
                    onChange={this.onChange}
                    disabled
                  />
                  <Row>
                    <FormGroup as={Col}>
                      <FormLabel>Consultants</FormLabel>
                      {consultants.map(({ id, firstName, lastName }) => {
                        const value = selectedConsultants.find(cons => cons === id)
                        const name = `${firstName} ${lastName}`
                        return (
                          <Row>
                            <FormGroup as={Col}>
                              <label className="ms-checkbox-wrap">
                                <FormControl
                                  type="checkbox"
                                  onChange={this.onChange}
                                  className={`form-check-input ${value ? "checked" : ""}`}
                                  name={name}
                                  value={id}
                                  checked={value}
                                />
                                <i className="ms-checkbox-check"></i>
                              </label>
                              <span>{name}</span>
                            </FormGroup>
                          </Row>
                        )
                      })}
                    </FormGroup>
                  </Row>
                  <TextField
                    name="siteAddressName"
                    label="Site Address"
                    value={siteAddressName}
                    placeholder="Ex: 33 Holborn, London, UK"
                    onChange={this.onChange}
                  />
                  <TextField
                    name="sitePhoneNumber"
                    label="Site Phone Number"
                    value={sitePhoneNumber}
                    placeholder="Ex: 7869382393"
                    onChange={this.onChange}
                  />
                  <TextareaField name="description" label="Description" value={description} onChange={this.onChange} />
                  <Row className="justify-content-end">
                    <Col lg={6} className="mt-3">
                      <Button variant="primary" type="submit" className="w-25 float-right">
                        Update
                      </Button>
                    </Col>
                  </Row>
                </Form>
              </div>
            </div>
          </Col>
        </Row>
      </section>
    )
  }
}

EditProject.propTypes = {}

const mapStateToProps = (state, props) => {
  return {
    project: selectProject(state, props.computedMatch.params.id),
    consultants: selectConsultants(state),
    loading: loadingProject(state),
    error: projectErrors(state),
  }
}

export default withRouter(
  connect(mapStateToProps, {
    getProjects,
    getConsultants,
    updateProject,
  })(EditProject)
)
