import React, { Component } from "react"
import { Col, Row } from "react-bootstrap"
import { getProjects } from "@actions/project"
import { selectProject, loadingProject, projectErrors } from "@selectors/project"
import { connect } from "react-redux"
import Sidebar from "../../Sidebar"
import { Breadcrumb } from "@components/shared/Breadcrumb"
import { Loading } from "@components/shared/Loading"
import { Link } from "react-router-dom"
import Comments from "./Comments"
import History from "./History"
import Quotes from "../Quotes"
import moment from "moment"
import styles from "./Projects.module.css"

class ViewProject extends Component {
  state = {
    loading: true,
    isModalOpen: null,
  }

  componentDidMount() {
    this.props.getProjects()
  }

  onChange = e => {
    this.setState({ [e.target.name]: e.target.value })
  }

  padId = number => Array(Math.max(5 - String(number).length + 1, 0)).join(0) + number

  render() {
    const {
      loading,
      project: {
        id,
        description,
        addressBook: { addressName, companyName, users },
        site,
        consultants,
        servicePrices,
        consultant: { firstName, lastName },
        createdAt,
        history,
      },
      project,
    } = this.props

    if (loading) return <Loading />
    if (!project) return null
    return (
      <section className="main">
        <Row>
          <Col>
            <Sidebar />
          </Col>
          <Col lg="11" className="container-left">
            <Breadcrumb />
            <div className="ms-panel">
              <div className={`ms-panel-header ${styles.headermini}`}>
                <div className={`d-flex justify-content-between ${styles.header}`}>
                  <h6>Project</h6>
                  <h6>#{this.padId(id)}</h6>
                </div>
              </div>
              <div class="ms-panel-body">
                <div class="row align-items-center">
                  <div class="col-md-6">
                    <div className={styles.invoiceaddress}>
                      <h5>{companyName}</h5>
                      <p>{addressName}</p>
                    </div>
                  </div>
                  <div class="col-md-6 text-md-right">
                    <ul class="invoice-date">
                      <li>Created Date: {moment(createdAt).format("LLLL")}</li>
                      <li>
                        <h5 className={styles.byconsultant}>
                          {firstName} {lastName}
                        </h5>
                      </li>
                    </ul>
                  </div>
                </div>
                <div className={styles.categoriesconsultants}>
                  <h2>Consultants</h2>
                  <ul>
                    {consultants.map(({ firstName, lastName }) => (
                      <li>
                        {firstName} {lastName}
                      </li>
                    ))}
                  </ul>
                </div>
                <div className={styles.categoriesconsultants}>
                  <h2>Services</h2>
                  <ul>
                    {servicePrices.map(({ name }) => (
                      <li>{name}</li>
                    ))}
                  </ul>
                </div>
                <div className={styles.barier}>
                  <h2>Site Address</h2>
                  <p>{site.addressName}</p>
                </div>
                <div className={styles.barier}>
                  <h2>Site Phone Number</h2>
                  <p>{site.phoneNumber}</p>
                </div>
                <div className={styles.barier}>
                  <h2>Users</h2>
                  <div class="row">
                    {users.map(({ firstName, lastName, role, email1, phoneNumber1 }) => (
                      <div class="col-xl-4 col-md-6 col-sm-12">
                        <div className={`media ms-profile-experience ${styles.user}`}>
                          <div class="mr-2 align-self-center">
                            <img
                              src="https://via.placeholder.com/270x270"
                              class="ms-img-round ms-img-small"
                              alt="people"
                            />
                          </div>
                          <div class="media-body">
                            <h4>
                              {firstName} {lastName} - {role.name}
                            </h4>
                            <p>{email1}</p>
                            <p>{phoneNumber1}</p>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
                <div className={styles.barier}>
                  <h2>Status</h2>
                  <p>{history[0].status.name}</p>
                </div>
                <div className={styles.barier}>
                  <h2>Description</h2>
                  <p>{description}</p>
                </div>
                <div class="invoice-buttons text-right">
                  <Link to={`/dashboard/project/${id}/create-quote`} className={`btn btn-primary mr-2 ${styles.btn}`}>
                    Create a Quote
                  </Link>
                </div>
              </div>
            </div>
            <Row>
              <Col>
                <Quotes projectId={id} users={users} />
              </Col>
              <Col>
                <History {...this.props} />
              </Col>
            </Row>
            <Comments projectId={id} />
          </Col>
        </Row>
      </section>
    )
  }
}

ViewProject.propTypes = {}

const mapStateToProps = (state, props) => {
  return {
    project: selectProject(state, props.computedMatch.params.id),
    loading: loadingProject(state),
    error: projectErrors(state),
  }
}

export default connect(mapStateToProps, {
  getProjects,
})(ViewProject)
