import React, { lazy, Component } from "react"
import { Route, Switch, Redirect } from "react-router-dom"
import { connect } from "react-redux"
import { withRouter } from "react-router-dom"
import { restoreSession } from "@actions/authorization"
import Login from "@components/Authorization/Login"
import Reset from "@components/Authorization/Reset"
import Forgot from "@components/Authorization/Forgot"
import Routes from "@components/Routes"
import NotFound from "@components/NotFound"
import AddProject from "@components/Dashboard/Project/Projects/AddProject"
import EditProject from "@components/Dashboard/Project/Projects/EditProject"
import ViewProject from "@components/Dashboard/Project/Projects/ViewProject"

const Consultants = lazy(() => import("@components/Dashboard/Consultants"))
const ChangePassword = lazy(() => import("@components/Dashboard/ChangePassword"))
const Type = lazy(() => import("@components/Dashboard/Inventory/Type"))
const Manufacture = lazy(() => import("@components/Dashboard/Inventory/Manufacture"))
const Model = lazy(() => import("@components/Dashboard/Inventory/Model"))
const Hardware = lazy(() => import("@components/Dashboard/Inventory/Hardware"))
const Kit = lazy(() => import("@components/Dashboard/Inventory/Kit"))
const Issues = lazy(() => import("@components/Dashboard/Issues"))
const ProjectQuotes = lazy(() => import("@components/Dashboard/Project/Quotes"))
const CreateQuote = lazy(() => import("@components/Dashboard/Project/Quotes/CreateQuote"))
const EditQuote = lazy(() => import("@components/Dashboard/Project/Quotes/EditQuote"))
const QuoteService = lazy(() => import("@components/Dashboard/Project/Services"))
const AdddressBook = lazy(() => import("@components/Dashboard/Project/AddressBook"))
const Projects = lazy(() => import("@components/Dashboard/Project/Projects"))
const Statistics = lazy(() => import("@components/Dashboard/Statistics"))
/*     
  <AuthorizedRoute path="/dashboard/issues" admin={false} component={Issues} />
  <AuthorizedRoute path="/dashboard/projects" admin={false} component={Projects} />
  <AuthorizedRoute path="/dashboard/project-category" admin={false} component={ProjectCategory} />

https://codesandbox.io/s/610p2v4x4n?file=/src/index.js

*/

class App extends Component {
  componentDidMount() {
    this.props.restoreSession()
  }

  render() {
    return (
      <Switch>
        <Routes exact path="/" component={Login} authorized={false} />
        <Routes path="/login" component={Login} authorized={false} />
        <Routes path="/reset" component={Reset} authorized={false} />
        <Routes path="/forgot" component={Forgot} authorized={false} />
        <Routes path="/dashboard/change-password" component={ChangePassword} authorized={true} />
        <Routes path="/dashboard/consultants" component={Consultants} authorized={true} />
        <Routes path="/dashboard/inventory-kit" component={Kit} authorized={true} />
        <Routes path="/dashboard/inventory-hardware" component={Hardware} authorized={true} />
        <Routes
          path="/dashboard/inventory-type"
          roles={["admin", "technical-manager"]}
          component={Type}
          authorized={true}
        />
        <Routes
          path="/dashboard/inventory-manufacture"
          roles={["admin", "technical-manager"]}
          component={Manufacture}
          authorized={true}
        />
        <Routes
          path="/dashboard/inventory-model"
          roles={["admin", "technical-manager"]}
          component={Model}
          authorized={true}
        />
        <Routes path="/dashboard/issues" component={Issues} authorized={true} />
        <Routes path="/dashboard/projects" component={Projects} authorized={true} />
        <Routes path="/dashboard/project/add" component={AddProject} authorized={true} />
        <Routes path="/dashboard/project/:id" exact component={ViewProject} authorized={true} />
        <Routes path="/dashboard/project/:id/edit" component={EditProject} authorized={true} />
        <Routes path="/dashboard/project-services" component={QuoteService} authorized={true} />
        <Routes path="/dashboard/project-quotes" component={ProjectQuotes} authorized={true} />
        <Routes path="/dashboard/project/:id/create-quote" component={CreateQuote} authorized={true} />
        <Routes path="/dashboard/project/:projectId/quote/:quoteName" component={EditQuote} authorized={true} />
        <Routes path="/dashboard/address-book" component={AdddressBook} authorized={true} />
        <Routes path="/dashboard/statistics" component={Statistics} authorized={true} />
        <Redirect from="/dashboard" to="/dashboard/consultants" />
        <Route component={NotFound} />
      </Switch>
    )
  }
}

export default withRouter(connect(null, { restoreSession })(App))
