import React, { Suspense } from "react"
import { Route } from "react-router-dom"
import { connect } from "react-redux"
import { selectAuthorization } from "@selectors/authorization"
import AuthorizedRoute from "@components/Routes/AuthorizedRoute"
import UnAuthorizedRoute from "@components/Routes/UnAuthorizedRoute"
import { Loading } from "@components/shared/Loading"

class Routes extends Route {
  state = {
    roleName: "admin",
  }

  render() {
    const { authorized } = this.props
    const { roleName } = this.state
    return authorized ? (
      <Suspense fallback={<Loading />}>
        <AuthorizedRoute {...this.props} roleName={roleName} />
      </Suspense>
    ) : (
      <Suspense fallback={<Loading />}>
        <UnAuthorizedRoute {...this.props} />
      </Suspense>
    )
  }
}

const mapStateToProps = state => ({
  authorization: selectAuthorization(state),
})

export default connect(mapStateToProps)(Routes)
