import React from "react"
import { Route, Redirect } from "react-router-dom"
import Header from "@components/Header"

class AuthorizedRoute extends Route {
  render() {
    const { authorization, component: Component, roles, roleName } = this.props
    //  console.log(roleName)
    //console.log("roles", roles)
    if (authorization.isAuthorized) {
      if (true) {
        //|| roles.includes(roleName)
        //if (!roles || roles.includes(authorization.data.roleName)) {
        return (
          <>
            <Header />
            <Component {...this.props} />
          </>
        )
      } else {
        return <Redirect to="/dashboard/consultants" />
      }
    } else {
      return <Redirect to="/login" />
    }
  }
}

export default AuthorizedRoute

